.gpt3__blog{
    display: flex;
    flex-direction: column;
}

.gpt3__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.gpt3__blog-heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
    text-align: left;
}

.gpt3__blog-container {
    display: flex;
    flex-direction: row;
}

.gpt3__blog-container-groupA {
    flex: 0.75;
    margin-right: 2rem;
}

.gpt3__blog-container-groupB {
    flex: 1;
    display:grid;
    grid-template-columns: repeat(2,1fr);
    gap: 2rem;
}

@media screen and (max-width: 990px){
    .gpt3__blog-container {
        flex-direction: column-reverse;
    }
    .gpt3__blog-container-groupA {
        margin: 2rem 0;
    }

    .gpt3__blog-container-groupA .gpt3__blog-container-article {
        width: 48%;
    }
    .gpt3__blog-container-groupA .gpt3__blog-container-article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px){
    .gpt3__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3__blog-container_groupA .gpt3__blog-container-article {
        width: 100%;
    }

    .gpt3__blog-heading {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px){
    .gpt3__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
}